@import '../../../base';

%standard-font {
  font-family: $font-open-sans;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  text-align: center;
  color: white;
}
$heavy-weight: 700;
$horizontal-anim-duration: 90s;
$vertical-anim-duration: 4.5s;

.info-page {
  h5 {
    font-weight: $font-weight-semibold;
  }

  .no-margin {
    margin-left: 0;
    margin-right: 0;
    padding: 0 !important;
    max-width: none;
  }

  .background-overlay {
    position: relative;

    .overlay {
      width: 100vw;
      object-fit: contain;
      mix-blend-mode: multiply;
      will-change: opacity;
    }
  }

  .content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    // align-items: center;
    text-align: left;

    p,
    h5 {
      margin: 0;
    }

    h1 {
      margin-bottom: 10px;
    }

    h5 {
      margin-bottom: 20px;
    }
  }

  .main-img {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 37px;
    @media screen and(max-width: 991px) {
      padding-left: 0;
    }

    img {
      width: 100%;
      max-width: 650px;
      height: auto;
      margin: 30px 0;
      // @media screen and(max-width: 767px) {
      //   margin: 0;
      // }
    }
  }

  .subscribe-container {
    margin: 20px auto 0 auto;
    transform: translateY(185px);
    background: $color-input-form-landing-desc3;
    box-shadow: 0px 40px 30px rgba(0, 0, 0, 0.2);
    border-radius: 20px;
    min-height: 277px;
    position: relative;
    z-index: 2;
    max-width: 1200px;

    @media screen and(max-width: 767px) {
      margin-top: -250px;
      transform: translateY(320px);
    }

    @media screen and(max-width: 575px) {
      min-height: 0;
      padding: 20px;
    }


    .subscribe-main-img {
      width: auto;
      position: absolute;
      bottom: 0;
      left: 55px;

      @media screen and(max-width: 980px) {
        max-width: 300px;
        left: 25px;
      }

      @media screen and(max-width: 767px) {
        max-width: 250px;
        left: 25px;
      }

      @media screen and(max-width: 640px) {
        max-width: 200px;
        left: 10px;
      }
    }

    .right-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      padding-left: 10px;

      @media screen and(max-width: 575px) {
        padding: 0;
        align-items: center;
      }

      @media screen and(min-width: 630px) {
        padding-left: 50px;
      }


      h3 {
        font-weight: $font-weight-bold;
        font-size: $font-size-small + $font-size-extra-tiny;
        color: white;
      }
  
      h5 {
        font-weight: $font-weight-normal;
        font-size: $font-size-smallest;
        color: white;
        max-width: 270px;
        margin-bottom: 25px;
      }
  
      .store-btn {
        width: auto;
        height: 45px;
        transform: translateX(-8px);
      }

      a {
        padding: 2px;
        img {
          transition: transform 0.3s ease;
          transform-origin: center;
          &:hover {
            transform:  translateX(-8px) scale(1.05);
          }
          &:active {
            transform:  translateX(-8px) scale(1);
          }
        }
      }
    }
  }
}

.info-page-margin {
  max-width: 1110px;
  padding: 0 40px;
  margin: auto;
  width: 100%;
  
  @media screen and (max-width: 991px) {
    padding: 0 15px;
  }
}