@import '../../base';

.referral-info {
  .content {
    .referral-code {
      padding: 9px 9px 13px 9px;
      margin-top: 22px;
      background: white;
      /* Mild shadow */
      box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05);
      border-radius: 20px;
      text-align: center;
    }

    .unique {
      font-weight: $font-weight-bold;
      font-size: $font-size-extra-tiny;

      color: $color-hover;
    }

    .referral-desc {
      font-weight: $font-weight-semibold;
      font-size: $font-size-tiny;

      color: $color-input-title-inactive;
      text-align: center;
    }

    .code {
      line-height: 1;
      font-weight: $font-weight-bold;
      font-size: $font-size-extra-tiny * 3;

      color: $color-input-form-landing-desc2;
    }
    
    .sign-in {
      padding: 20px 0;
      b {
        color: $color-hover;
      }

      .code {
        font-size: $font-size-smallest;
      }

      button:hover {
        b {
          color: $color-hover-alt;
        }
      }
    }
  }
  
  .how-container {
    display: flex;
    flex-direction: column;
    align-items: center;

    img {
      width: 100%;
      max-width: 310px;
      height: auto;
    }
    h3,
    h5 {
      max-width: 280px;
      // margin: auto;
    }
    h3 {
      text-align: center;
      margin-bottom: 20px;
    }
  }
}