@import '../../../base';

.tos-container {
  position: relative;
  z-index: 3;
  
  @media screen and(min-width: 992px) {
    margin: 0 20px;
  }
  cursor: pointer;
  padding: 10px;
  background-image: linear-gradient(#ffffff, #ffffff);
  box-shadow: 0 20px 15px 0 rgba(70, 115, 122, 0.15);

  .nav-link {
    color: $color-title;
    font-weight: $font-weight-heavy;
    border: none;
  }

  .arrow{
    transform-origin: 50%;
    transition: 0.2s ease-in-out;
    width: 25px;

    @media screen and(min-width: 820px) {
      width: 35px;
    }
  }
  .turn{
    transform-origin: 50%;
    -ms-transform: rotate(180deg) !important;
    -webkit-transform: rotate(180deg) !important;
    transform: rotate(180deg) !important;
    transition: 0.2s ease-in;
    width: 25px;

    @media screen and(min-width: 820px) {
      width: 35px;
    }
  }
}

.tos-inner {
  position: relative;
  z-index: 3;
  // margin-bottom: 200px;
  @media screen and(min-width: 992px) {
    margin-left: 20px;
    margin-right: 20px;
  }
  .card {
    border-radius: 0;

    .card-body {
      padding: 10px;

      @media screen and(min-width: 820px) {
        padding: 1.25rem;
      }
    }
  }

  ol {
    padding-left: 20px;
    color: $color-body;
    font-size: $font-size-smallest;

    li {
      padding: 5px;
    }
  }
}
