@import '../../../base';

.form-landing-information-container {
  position: relative;
  z-index: 1;
  margin: 40px 0;

  @media screen and (max-width: 576px) {
    max-width: 350px;
    margin-left: auto;
    margin-right: auto;
  }

  @media screen and (min-width: 769px) {
    margin: 40px 0;
  }

  h2 {
    text-align: center;
  }

  .main-title {
    font-weight: $font-weight-heavy;
    font-size: $font-size-big * 5;
    mix-blend-mode: multiply;
    color: $color-input-form-landing-desc-title;

    @media screen and (max-width: 992px) {
      font-size: $font-size-big * 4;
    }

    @media screen and (max-width: 768px) {
      display: none;
    }
  }

  .sub-title {
    margin-top: -14px;
    transform: translateY(-50px);

    @media screen and (max-width: 576px) {
      margin-left: 5vw;
    }

    @media screen and (max-width: 768px) {
      margin-top: 0;
      transform: none;
      max-width: 183px;
      text-align: left;
      margin-left: 15px;
    }

    @media screen and (min-width: 769px) {
      color: $color-title !important;
    }

    &.center {
      text-align: center;
      max-width: none;
      margin-left: 0px;
    }
  }

  .information-container {
    display: flex;
    justify-content: center;
    align-items: stretch;
    flex-wrap: wrap;

    .desc-container {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      text-align: center;
      padding: 0 10px;

      @media screen and (max-width: 576px) {
        width: 50% !important;
      }

      .img-container {
        width: 160px;
        height: 160px;
        background: white;
        border-radius: 50%;
        /* Mild shadow */
        box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05);

        display: flex;
        justify-content: center;
        align-items: center;

        img {
          width: 50%;
          height: 50%;
        }

        @media screen and (max-width: 992px) {
          width: 130px;
          height: 130px;
        }

        @media screen and (max-width: 768px) {
          width: 100px;
          height: 100px;
        }
      }

      h3 {
        min-height: 42px;
        margin-top: 13px;
        margin-bottom: 19px;

        @media screen and (max-width: 991px) {
          min-height: 32px;
        }

        @media screen and (max-width: 576px) {
          min-height: 0;
        }
      }

      h5 {
        max-width: 200px;
        text-align: left;
        @media screen and (max-width: 768px) {
          max-width: 120px;
        }
      }
    }
  }
}

