@import '../../../base';

%circle-shape-default {
  position: absolute;
  top: 0;
  border-radius: 50%;
}

.landing-form-outer-container-new {
  max-width: 1110px;
  padding: 0 40px 0 40px !important;
  margin: auto;
  position: relative;
  margin-top: 52px;

  @media screen and (max-width: 575px) {
    padding: 0 15px 100px 15px !important;
  }

  @media screen and (max-width: 991px) {
    margin-top: 0;
  }

  .circle-shape {
    @extend %circle-shape-default;
    background-color: #F9FAFF;

    $circle-inside: 80%;
    $circle-margin: (100% - $circle-inside) * 0.5;
    &:after {
      content: '';
      position: absolute;
      top: $circle-margin;
      left: $circle-margin;
      width: $circle-inside;
      height: $circle-inside;
      background-color: white;
      border-radius: 50%;
    }
  }

  .circle-upper {
    right: 0;
    width: 1200px;
    height: 1200px;
    float: right;
    transform: translateX(40%) translateY(-60%);
  }

  .circle-bottom {
    left: 0;
    width: 2255px;
    height: 2255px;
    transform: translateX(-60%) translateY(448px);
  }

  .circle-green {
    @extend %circle-shape-default;
    right: 0;
    width: 239px;
    height: 239px;
    background-color: rgba(0, 215, 175, 0.1);
    transform: translateX(-150%) translateY(304px);
  }

  @keyframes float {
    0% {
      transform: translateY(0px);
    }
    50% {
      transform: translateY(20px);
    }
    100% {
      transform: translateY(0px);
    }
  }

  
  .mouse {
    justify-content: center;
    img {
      animation: float 3s ease-in-out infinite;
    }
  }
}

.landing-form-container-new {
  max-width: 1006px;
  .edit-header {
    text-align: center;
    font-size: $font-size-big;
    font-weight: $font-weight-bold;
    color: $color-title;
  }

  .button-container {
    margin-top: 30px;

    .button-container-inner:nth-of-type(1) {
      button {
        margin-right: 10px;

        @media screen and (max-width: 575px) {
          margin-right: 0;
          margin-bottom: 5px;
        }
      }
    }
    .button-container-inner:nth-of-type(2) {
      button {
        margin-left: 10px;

        @media screen and (max-width: 575px) {
          margin-left: 0;
          margin-top: 5px;
        }
      }
    }
  }
}

.landing-form-container-border {
  background: white;
  border-radius: 10px;
  margin: auto;
  width: 100%;

  @media screen and (min-width: 576px) {
    padding: 30px;
    box-shadow: 0px 50px 40px rgba(0, 0, 0, 0.05);
  }

  @media screen and (min-width: 992px) {
    width: 458px;
    margin-left: -60px;
  }

  @media screen and (min-width: 1180px) {
    margin-left: 0;
  }
}